<template>
   <div class="code-view-container">
        <div class="block-title">{{title}}</div>
        <codemirror
            ref="mycode"
            :value="curCode"
            :options="cmOptions"
            class="code">
        </codemirror>
    </div>
</template>

<script>
import { codemirror } from 'vue-codemirror'
import "codemirror/theme/rubyblue.css";  // theme
import 'codemirror/lib/codemirror.css';
require("codemirror/mode/javascript/javascript"); // jsmode
// keymap
import 'codemirror/addon/comment/comment'
import 'codemirror/keymap/sublime'
export default {
    name: "codeExmaple",
    data() {
        return {
            editor: null
        }
    },
    props:{
        title:{
            type: String,
            default: ''
        },
        curCode:{
            type: String,
            default: ''
        },
        cmOptions:{
            type: Object,
            default:function(){
                return {
                    value:'',
                    mode:"text/javascript",
                    tabSize: 2, // Tab
                    theme: "rubyblue", // 
                    lineNumbers: true, //
                    readOnly:true,  //
                    lineNumbers:true, //
                };
			}
        }
    },
    mounted() {
    },
    components:{
        codemirror
    }
}
</script>

<style scoped lang="scss">
.block-title {
     font-family: 'zhiyongshoushu'; 
}
// 
/deep/ .CodeMirror {
    border: 1px solid #eee;
    height: auto;
}

/deep/ .CodeMirror-scroll {
    height: auto;
    overflow-y: hidden;
    overflow-x: auto;
}
</style>


