<template>
   <div class="js-page-container">
        <div class="block">
            <codeExmaple :cur-code="curCode" title=""></codeExmaple>
        </div>
        <div class="block">
            <codeExmaple :cur-code="curCode2" title=""></codeExmaple>
        </div>
        <div class="block">
            <codeExmaple :cur-code="curCode3" title=""></codeExmaple>
        </div>
        <div class="block">
            <codeExmaple :cur-code="curCode4" title="data-* "></codeExmaple>
        </div>
   </div>
</template>

<script>
import codeExmaple from '../../components/codeExmaple.vue';
export default {
    name: "JsPage",
    data() {
        return {
            editor: null,
            curCode:"const array  = [5,4,7,8,9,2];\n"+"array.reduce((a,b) => a+b);\n"+"// : 35",
            curCode2:"const array  = [5,4,7,8,9,2];\n"+"array.reduce((a,b) => a>b?a:b);\n"+"// : 9",
            curCode3: "const array  = [5,4,7,8,9,2];\n"+"array.reduce((a,b) => a<b?a:b);\n"+"// : 2",
            curCode4: "data-* JavaScript \n"+
                        "data-* \n"+
                        "data-\n"+
                        "\n\n"+
                        "<div id='data-attr' data-custom-attr='You are just Awesome!'></div>\n\n"+
                        " JavaScript \n"+
                        "let value = document.getElementById('data-attr').dataset['customAttr']\n\n"+
                        " JavaScript getAttribute() HTML  data-custom-attrdataset"
        }
    },
    mounted() {
    },
    components:{
        codeExmaple
    }
}

</script>

<style scoped lang="scss">
.block {
    padding: 20px;
    border:1px solid;
    margin-bottom: 12px;
}
.block-title {
    font-weight: bold;
    margin-bottom: 10px;
}
</style>


